body {
  background-color: #f4eade;
}
.App {
  text-align: center;
  flex-direction: column;
  display: flex;
  height: 100%;
}
.content {
  font-size: calc(10px + 2vmin);
  color: #303030;
}
.title {
  color: #5F5E5E;
}
@media screen and (max-width: 500px) {
  .App, .lists {
    flex-direction: column;
  }
}
.headshot-container {
  display:flex;
  justify-content:center; /* horizontally center */
  align-items:center;    /* vertically center */
}
.headshot {
  display: 'flex';
  max-width: 100%;
}
.left {
  float: 'left';
  flex: 1;
}
.right {
  float: 'right';
  flex: 1;
  padding: 2.5vw;
}
.lists {
  display: flex;
  flex-direction: row;
  flex:1;
}
.list {
  flex: 1;
  width: 100%;
  text-align: left;
}
.list ul {
  list-style-type: none;
  padding-left: 0;
}
.tagline {
  font-size: calc(10px + 3vmin);
  color: #c9c4bf;
}
a {
  color: #3D9BDA;
}
.papers-list {
  font-size: calc(10px + 1.5vmin);
}